import React, { useMemo, useEffect } from "react";
import styles from "./burgerIngredients.module.css";
import PropTypes from "prop-types";
import { Tab } from "@ya.praktikum/react-developer-burger-ui-components";
import { useDispatch, useSelector } from "react-redux";
import { useInView } from "react-intersection-observer";
import { setCurrentTab } from "../../services/actions/ingredientsList";
import uuid from "react-uuid";
import { BurgerIngredient } from "../burger-ingredient/BurgerIngredient";

const BurgerIngredients = ({ openModalIngredient }) => {
  const ingredients = useSelector((store) => store.ingredientsList.ingredients);
  const currentTab = useSelector((store) => store.ingredientsList.currentTab);

  const dispatch = useDispatch();
  const [bun, inViewBun] = useInView({
    threshold: 0.2,
  });
  const [sauce, inViewSauce] = useInView({
    threshold: 0.2,
  });
  const [main, inViewMain] = useInView({
    threshold: 0.2,
  });

  useEffect(() => {
    if (inViewBun) {
      dispatch(setCurrentTab("bun"));
    } else if (inViewSauce) {
      dispatch(setCurrentTab("sauce"));
    } else if (inViewMain) {
      dispatch(setCurrentTab("main"));
    }
  }, [inViewBun, inViewSauce, inViewMain, dispatch]);

  const buns = useMemo(
    () => ingredients.filter((item) => item.type === "bun"),
    [ingredients]
  );
  const mains = useMemo(
    () => ingredients.filter((item) => item.type === "main"),
    [ingredients]
  );
  const sauces = useMemo(
    () => ingredients.filter((item) => item.type === "sauce"),
    [ingredients]
  );
  const tabClick = (value) => {
    document.querySelector(`.${value}`).scrollIntoView({ behavior: "smooth" });
  };
  return (
    <section className={styles.ingredients}>
      <h1 className="text text_type_main-large mt-10 mb-5"> </h1>
      <div className={`${styles.tab__bar} mb-10`}>
        <Tab
          value="bun"
          active={currentTab === "bun"}
          inViewBun={inViewBun}
          onClick={(value) => {
            dispatch(setCurrentTab(value));
            tabClick(value);
          }}
        >
          
        </Tab>

        <Tab
          value="sauce"
          active={currentTab === "sauce"}
          inViewSauce={inViewSauce}
          onClick={(value) => {
            dispatch(setCurrentTab(value));
            tabClick(value);
          }}
        >
          
        </Tab>
        <Tab
          value="main"
          active={currentTab === "main"}
          inViewMain={inViewMain}
          onClick={(value) => {
            dispatch(setCurrentTab(value));
            tabClick(value);
          }}
        >
          
        </Tab>
      </div>
      <ul className={`${styles.list_types} pl-2`}>
        <li ref={bun} className="bun">
          <h3 className="text text_type_main-medium mb-6"></h3>
          <ul className={styles.list_ingredients}>
            {buns.map((item) => (
              <BurgerIngredient
                key={item._id}
                item={item}
                openModalIngredient={openModalIngredient}
              />
            ))}
          </ul>
        </li>
        <li ref={sauce} className="sauce">
          <h3 className="text text_type_main-medium mt-10 mb-6"></h3>

          <ul className={styles.list_ingredients}>
            {sauces.map((item) => (
              <BurgerIngredient
                key={item._id}
                item={item}
                openModalIngredient={openModalIngredient}
              />
            ))}
          </ul>
        </li>
        <li ref={main} className="main">
          <h3 className="text text_type_main-medium mt-10 mb-6"></h3>
          <ul className={styles.list_ingredients}>
            {mains.map((item) => (
              <BurgerIngredient
                key={item._id}
                item={item}
                openModalIngredient={openModalIngredient}
              />
            ))}
          </ul>
        </li>
      </ul>
    </section>
  );
};
BurgerIngredients.propTypes = {
  openModalIngredient: PropTypes.func.isRequired,
};
export default BurgerIngredients;
